/* ÔøΩÔøΩÔøΩ ÇÃÉXÔøΩ^ÔøΩCÔøΩÔøΩ */
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.title {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 20px;
}

.header-image-container {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}

.header-image {
    max-width: 50%;
    cursor: pointer;
}

.image-container {
    width: 100%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 100px; /* ÔøΩeÔøΩÊëúÔøΩ‘Ç…ó]ÔøΩÔøΩÔøΩÔøΩ«âÔøΩ */
}

.image {
    width: 50%;
    height: auto;
    max-width: 100%;
    cursor: pointer;
    margin-bottom: 10px;
}

.input-container {
    display: flex;
    justify-content: center;
    width: 100%;
}

.input {
    flex: 1;
    padding: 10px;
    font-size: 1rem;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    max-width: 70%;
}

.button {
    padding: 10px 15px;
    font-size: 1rem;
    background-color: #000;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.correct-message {
    font-size: 1.2rem;
    color: green;
    margin-bottom: 10px;
}

.post-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: #000;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1rem;
    margin-bottom: 10px;
}

.incorrect-message {
    font-size: 1rem;
    color: red;
}

/* 5ÔøΩÔøΩ⁄ÇÔøΩÔøΩ\ÔøΩÔøΩÔøΩÔøΩÔøΩÍÇΩÔøΩ€ÇÃãÔøΩÔøΩÔøΩÔøΩXÔøΩ^ÔøΩCÔøΩÔøΩ */
