/*}*/

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: scale(0.9);
    }

    100% {
        opacity: 1;
        transform: scale(1);
    }
}

/* ÔøΩXÔøΩ}ÔøΩ[ÔøΩgÔøΩtÔøΩHÔøΩÔøΩÔøΩÔøΩÔøΩÔøΩÔøΩÃÉXÔøΩ^ÔøΩCÔøΩÔøΩ */
@media (max-width: 600px) {
    .title {
        font-size: 2rem;
    }

    .header-image {
        max-width: 50%;
    }

    .image-container {
        width: 100%;
        margin-bottom: 100px;
    }

    .image {
        max-width: 50%;
        margin-bottom: 10px;
    }

    .input {
        max-width: 100%;
        margin-right: 5px;
        font-size: 0.9rem;
    }

    .button {
        padding: 8px 10px;
        font-size: 0.9rem;
    }

    .correct-message {
        font-size: 1rem;
    }

    .post-button {
        padding: 8px 15px;
        font-size: 0.9rem;
    }

    .incorrect-message {
        font-size: 0.9rem;
    }

    .image-container.fifth {
        border-width: 2px; /* ÔøΩÔøΩÔøΩoÔøΩCÔøΩÔøΩÔøΩÔøΩÔøΩÔøΩÔøΩ…ògÔøΩÔøΩÔøΩÃåÔøΩÔøΩ›Çí≤êÔøΩ */
        box-shadow: 0 0 10px rgba(255, 152, 0, 0.6);
    }
}
